import User from '@/store/modules/user/user';
import { LOGIN, REGISTER } from '@/store/modules/user/mutation-types';
import { A_LOGIN, A_REGISTER, A_SAVE_USERCODE } from '@/store/modules/user/action-types';
import { getUserByUsername, postUser, updateUserByUsername } from '@/api';

const USER = {
  namespaced: true,
  state: {
    user: new User(),
  },
  getters: {
    username(state) {
      return state.user.username;
    },
    userCode(state) {
      return state.user.userCode;
    },
    isActive(state) {
      return state.user.isActive;
    },
  },
  mutations: {
    [REGISTER](state, payload) {
      const { username, userCode, isActive } = payload;

      state.user.register(username, userCode, isActive);
    },
    [LOGIN](state, payload) {
      const { username, userCode } = payload;

      state.user.login(username, userCode);
    },
  },
  actions: {
    [A_REGISTER]({ commit }, payload) {
      const { username, password, repeatPassword } = payload;

      if (password.length !== 0 && repeatPassword.length !== 0 && password === repeatPassword) {
        postUser(username, password)
          .then((response) => {
            console.log(response);

            const { userCode } = response.data;

            commit(REGISTER, {
              username,
              userCode,
              isActive: true,
            });
          })
          .catch((err) => {
            console.error(err);
          });
      } else {
        console.error('error on register: passwords dont match');
      }
    },
    // eslint-disable-next-line no-unused-vars
    [A_LOGIN]({ commit }, payload) {
      const { username, password } = payload;

      getUserByUsername(username)
        .then((response) => {
          console.log(response);

          const user = response.data;

          if (password === user.password) {
            commit(LOGIN, user);
          } else {
            console.error('Wrong password');
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },
    // eslint-disable-next-line no-unused-vars
    [A_SAVE_USERCODE]({ commit }, payload) {
      const { username, userCode } = payload;

      updateUserByUsername(username, userCode)
        .then((response) => {
          console.log(response);
          alert('Код сохранен');
        })
        .catch((err) => {
          console.error(err);
        });
    },
  },
};

export default USER;
