import axios from 'axios';

// eslint-disable-next-line import/prefer-default-export
export const HTTP = axios.create({
  baseURL: 'http://46.101.200.30',
  // baseURL: 'http://localhost:8086',
  headers: {
    // eslint-disable-next-line quote-props
    'Authorization': 'anonymous',
    'Content-Type': 'application/json',
    // eslint-disable-next-line quote-props
  },
});

export const getUsers = () => HTTP.get('/users');

export const getUserByUsername = username => HTTP.get(`/users/${username}`);

export const updateUserByUsername = (username, userCode) => {
  const config = { headers: { 'Content-Type': 'application/json' } };

  return HTTP.put(`/users/${username}`, { userCode }, config);
};

export const postUser = (username, password) => {
  const config = { headers: { 'Content-Type': 'application/json' } };

  return HTTP.post('/user', { username, password }, config);
};
