import { RUN_GAME, SUBMIT_CODE } from '@/store/modules/noughts-crosses-game/mutation-types';
import { HTTP } from '@/api';

const NOUGHTS_CROSSES_GAME = {
  namespaced: true,
  state: {
    userCode: '',
    steps: [],
  },
  mutations: {
    [SUBMIT_CODE](state, payload) {
      const { data = {} } = payload;
      const { userCode = '' } = data;

      state.userCode = userCode;
    },
    [RUN_GAME](state, payload) {
      const { data: steps = [] } = payload;

      state.steps = [...steps];
    },
  },
  actions: {
    submitCode({ commit }, payload) {
      const { userCode = '', opponentCode = '' } = payload;

      HTTP('/run', {
        method: 'POST',
        data: {
          userCode,
          opponentCode,
        },
      })
        .then(response => commit(RUN_GAME, response))
        .catch(error => console.error(error));
    },
  },
};

export default NOUGHTS_CROSSES_GAME;
