<template>
  <v-navigation-drawer
    :value="drawerIsOpen"
    app
    absolute
    temporary
    right
    @input="onDrawerStateChange"
  >
    <template v-if="userIsActive">
      <h1>
        Hi, {{ username }}
      </h1>
    </template>
    <template v-else>
      User is not active
    </template>
  </v-navigation-drawer>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import { SET_NAVIGATION_DRAWER_STATE } from '@/store/modules/common/app-navigation-drawer/mutation-types';

export default {
  name: 'AppNavigationDrawer',
  computed: {
    ...mapState({
      drawerIsOpen(state) {
        return state.common.appNavigationDrawer.isOpen;
      },
    }),
    ...mapGetters({
      userIsActive: 'user/isActive',
      username: 'user/username',
    }),
  },
  methods: {
    onDrawerStateChange(value) {
      if (this.drawerIsOpen !== value) {
        this.$store.commit({
          type: `appNavigationDrawer/${SET_NAVIGATION_DRAWER_STATE}`,
          value,
        });
      }
    },
  },
};
</script>

