<template>
  <v-app-bar
    app
    flat
  >
    <v-container v-if="$vuetify.breakpoint.lgAndUp">
      <v-row no-gutters>
        <v-col class="d-flex align-center">
          <span>LOGOTYPE</span>
        </v-col>

        <v-col cols="7">
          <AppNavBar />
        </v-col>

        <v-col class="d-flex align-center justify-center">
          <template v-if="userIsActive">
            Hi, {{ username }}
          </template>
          <template v-else>
            <v-btn
              class="primary"
              @click="openAppNavigationDrawer"
            >
              <span>Авторизация/Регистрация</span>
            </v-btn>
          </template>
        </v-col>
      </v-row>
    </v-container>

    <template v-else>
      <v-toolbar-title>LOGOTYPE</v-toolbar-title>
      <v-spacer />
      <v-btn
        icon
        @click="openAppNavigationDrawer"
      >
        <v-icon>fas fa-bars</v-icon>
      </v-btn>
    </template>
  </v-app-bar>
</template>

<script>
import AppNavBar from '@/components/AppNavBar';
import { SET_NAVIGATION_DRAWER_STATE } from '@/store/modules/common/app-navigation-drawer/mutation-types';
import { mapGetters } from 'vuex';

export default {
  name: 'AppHeader',
  components: { AppNavBar },
  computed: {
    ...mapGetters({
      userIsActive: 'user/isActive',
      username: 'user/username',
    }),
  },
  methods: {
    openAppNavigationDrawer() {
      this.$store.commit({
        type: `appNavigationDrawer/${SET_NAVIGATION_DRAWER_STATE}`,
        value: true,
      });
    },
  },
};
</script>

