import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home,
  },
  {
    path: '/choose-game',
    name: 'choose-game',
    component: () => import(/* webpackChunkName: "choose-game" */ '@/views/ChooseGame'),
    children: [
      {
        path: '/choose-game/all-games',
        name: 'all-games',
        component: () => import(/* webpackChunkName: "choose-game" */ '@/views/AllGames'),
      },
      {
        path: '/choose-game/ratings',
        name: 'ratings',
        component: () => import(/* webpackChunkName: "choose-game" */ '@/views/Ratings'),
      },
      {
        path: '/choose-game/noughts-crosses-game',
        name: 'noughts-crosses-game',
        component: () => import(/* webpackChunkName: "noughts-crosses-game" */ '@/views/NoughtsCrossesGame'),
      },
    ],
  },
  {
    path: '/register',
    name: 'register',
    component: () => import(/* webpackChunkName: "register" */ '@/views/RegisterPage'),
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;
