import {
  CHANGE_NAVIGATION_DRAWER_STATE,
  SET_NAVIGATION_DRAWER_STATE,
} from '@/store/modules/common/app-navigation-drawer/mutation-types';

const APP_NAVIGATION_DRAWER = {
  namespaced: true,
  state: {
    isOpen: false,
  },
  mutations: {
    [SET_NAVIGATION_DRAWER_STATE](state, payload) {
      const { value = false } = payload;
      state.isOpen = value;
    },
    [CHANGE_NAVIGATION_DRAWER_STATE](state) {
      state.isOpen = !state.isOpen;
    },
  },
};

export default APP_NAVIGATION_DRAWER;
